$color-main: #0056A9;
$color-red: #C23349;
$color-green: #5CAB5F;
$color-dark-blue: #32449F;
// $color-white: #ddd
$color-white: #ececec;



body {
    background: black; }


.registration {
    min-height: 710px;
    margin: 0 auto;
    background: url("../images/car-service2.png"), $color-main 50%;
    background-repeat: no-repeat;
    background-blend-mode: screen;
    &__form {
        display: flex;
        flex-direction: column;
        align-items: center; }

    &__without-button {
        height: 670px;
        width: 1420px;
        margin: 0 auto;
        color: $color-white;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between; }
    &__box-services {
        height: 605px;
        width: 640px;
        background-color: #525252;
        border-radius: 6px; }
    &__box-time-choice {}
    &__without-button {} }



.services {
    &__title {
        margin-top: 20px;
        margin-bottom: 5px;
        text-align: center;
        font-size: 25px;
        height: 35px; }
    &__border {
        margin: 0 auto;
        margin-top: 5px;
        height: 595px;
        width: 630px;
        border: 1px solid $color-white;
        border-radius: 6px; }
    &__p {
        text-indent: 20px; } }

table {
    margin: 0 auto;
    margin-bottom: 20px;
    text-align: left; }

td {
    padding: 3px;
    font-size: 16px; }

tr:hover {
    background-color: #494949; }

.day {
    display: inline-flex;
    height: 605px;
    width: 100px;
    background-color: #525252;
    margin-right: 3px;
    border-radius: 6px;
    &__date {
        margin: 0 auto;
        text-align: center;
        padding: 10px;
        font-size: 16px;
        width: 70px; }
    &__border {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        margin-top: 5px;
        height: 570px;
        width: 90px;
        // border: 1px solid $color-white
        input {
            type: radio;
            display: none; }
        input:checked + label {
            background: #ffe0a6;
            color: #2058ad; }
        input:disabled + label {
            background: #c5c5c5;
            color: #666; }
        label {
            // cursor: pointer
            text-align: center;
            height: 22px;

            line-height: 22px;
            border: 1px solid #e1e1e1;
            border-radius: 6px;
            user-select: none;
            margin-bottom: 2px /* отодвигаем кнопки время вниз друг от друга */; }
        label:hover {
            color: #a20d0d; } } }








